/* === Typography === */

@import url(http://fonts.googleapis.com/css?family=Roboto:400,300,500,700|Roboto+Slab:400,300,700);

html	{
	font-family: $body-font;
}

blockquote	{
	font-family: $serif;
	font-weight: 200;
	text-align: left;

	*	{
		width: 100%;
	}

	p	{
		font-family: $serif;
		font-weight: 200;
	}

	&.large-blockquote	{

		.design-quote	{
			font-family: $sans-serif;
			font-size: 1.6rem;
			color: $grey;
			text-transform: uppercase;
			font-weight: 600;
		}

		p	{
			font-size: 3.2rem;
		}

		.byline	{
			font-size: 2.2rem;
			text-align: right;
		}

	}

}